.App {
  text-align: center ;
  background-color:rgb(243, 220, 102);

}

.header {
  text-align: center;
  font-size: xx-large;
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  padding-top: 20px;
}
.control{
  padding: 10px;
}
.button{
  width: 100px;
  height: 30px;
  margin-left: 20px;
  border-radius: 3px;
  box-shadow: black 0px 3px 20px;
}
.tail{
  padding-top: 5px;
  display: flex;
  justify-content: center;
  padding: 20px;
  font-size: large;
  font-family:Verdana, Geneva, Tahoma, sans-serif;
}
.tail1{
  padding-right: 100px;
}
.restart{
  background-color: transparent;
  border: 0px;
  padding-left: 100px;
  font-size: large;
  font-family:Verdana, Geneva, Tahoma, sans-serif;
}
.App-header {
  background-color: #963328;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

